<template>
  <div class="code">
    <div class="productContent">
      <div class="productBanner">
        <img src="../../assets/banner24.jpg" />
        <div class="productBannerContent">
          <!--  -->
          <h3></h3>
          <article>
            
          </article>
          <router-link
            to="/Gain"
            class="productBannerUse"
            :class="{ getUseSelect: getUseIndex == 1 }"
            @mouseover.native="getUse()"
            @mouseout.native="recovergetUse()"
            ></router-link
          >
        </div>
      </div>
      <!--  -->
      <div class="productBox">
        <div class="productIntroduce">
          <h3></h3>
          <p>
            LLVMC/C++C/C++C/C++C/C++
          </p>
        </div>
        <!--  -->
        <div class="produtctTrait">
          <h3></h3>
          <ul class="productTraitContent">
            <li v-for="(item, index) in traitList" :key="index">
              <div class="productTraitContentTop">
                <img :src="item.imgSrc" />
                <p>{{ item.title }}</p>
              </div>
              <div class="productTraitContentContainer">
                <span>{{ item.content }}</span>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  name: "Evaluating",
  data() {
    return {
      traitList: [
        {
          id: 1,
          imgSrc: require("../../assets/img1.png"),
          title: "",
          content:
            ""
        },
        {
          id: 2,
          imgSrc: require("../../assets/img2.png"),
          title: "",
          content:
            ""
        },
        {
          id: 3,
          imgSrc: require("../../assets/img3.png"),
          title: "",
          content:
            ""
        },
        {
          id: 4,
          imgSrc: require("../../assets/img4.png"),
          title: "",
          content: ""
        }
      ],
      getUseIndex: 0
    };
  },
  methods: {
    getUse() {
      this.getUseIndex = 1;
    },
    recovergetUse() {
      this.getUseIndex = 0;
    }
  },
  mounted() {
    /* console.log(this.xx()) */
  }
};
</script>



