<template>
  <div id="app">
    <router-view v-if="isRouterAlive" />
  </div>
</template>
<script>
export default {
  name: "App",
  provide() {
    return {
      reload: this.reload,
      articleListItem: [
        {
          id: 1,
          imgSrc: require("./assets/article1.jpg"),
          title: "315SDK",
          time: "2020/07/20",
          content:
            "\n2018GDPR62029315\n31571650APP" +
            "50SDK9150\nAPP2019202090%APP315SDK\n  1.SDK" +
            "SDKSDKSDK\n 2.SDKAPPAPPAPP\n 3.APPAPP" +
            "APP "
        },
        {
          id: 2,
          imgSrc: require("./assets/article2.png"),
          title: ",|99",
          time: "2020/07/01",
          content: "..."
        },
        {
          id: 3,
          imgSrc: require("./assets/article3.png"),
          title: ",,",
          time: "2020/06/25",
          content: "..."
        },
        {
          id: 4,
          imgSrc: require("./assets/article4.png"),
          title: "|APP(2019)",
          time: "2020/06/05",
          content:
            "20191APPAPPAPPAPPAPPAPP"
        }
      ]
    };
  },
  data() {
    return {
      isRouterAlive: true //
    };
  },
  methods: {
    reload() {
      this.isRouterAlive = false;
      this.$nextTick(() => {
        this.isRouterAlive = true;
      });
    }
  },
  watch: {
    $route(to, from) {
      if (to.path) {
        this.reload();
      }
    }
  }
};
</script>
<style>
* {
  margin: 0;
  padding: 0;
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
  color: #333;
}
html,
body {
  height: 100%;
  min-width: 1240px;
}
#app {
  height: 100%;
}
</style>



